.about-hero {
	margin-top: -105px;
	min-height: 690px;
	position: relative;

	@include media(tablet-993) {
		margin-top: -135px;
		min-height: 600px;
	}

	h1 {
		@include media(tablet-993) {
			max-width: 550px;
		}
	}

	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		&::after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(
				0deg,
				rgba(26, 25, 25, 0.46),
				rgba(26, 25, 25, 0.46)
			);
		}

		img {
			height: 100%;
			width: 100%;
			object-fit: cover;
			position: absolute;
			z-index: -1;
		}
	}

	&__inner {
		&-left {
			@include media(tablet-993) {
				flex-basis: 0;
			}
		}

		&-right {
			margin-top: 199px;
			display: flex;
			flex-direction: column;

			@include media(tablet-993) {
				flex-basis: 100%;
				padding-left: 38px;
			}

			@include media(550) {
				padding-left: 17px;
			}
		}
	}

	&__button {
		width: 56px;
		height: 56px;
		border-radius: 50%;
		text-decoration: none;
		margin-top: 58px;

		&:hover {
			@media screen and (min-width: 1025px) {
				svg {
					fill: $gold;
					opacity: 1;
				}
			}
		}

		&-icon {
			width: 56px;
			height: 56px;
			fill: #fff;
			opacity: 0.9;
			transition: 0.4s;
		}
	}
}
