.about-geography {
	background-color: $dark;
	padding-top: 154px;
	padding-bottom: 60px;

	@include media(tablet-993) {
		padding-top: 60px;
		padding-bottom: 30px;
	}

	&__inner {
		height: 660px;

		@include media(tablet-993) {
			height: auto;
		}
		&-left {
			@include media(tablet-993) {
				flex-basis: 0;
			}
		}
		&-right {
			position: relative;
			flex-direction: column;

			@include media(tablet-993) {
				flex-basis: 100%;
				padding-left: 38px;
			}

			@include media(550) {
				padding-left: 17px;
			}
		}
	}

	.section-title {
		font-family: $secondaryFont;
		font-weight: bold;
		font-size: 66px;
		line-height: 66px;

		@include media(1430) {
			max-width: 300px;
		}

		@include media(tablet-993) {
			font-size: 45px;
			line-height: 45px;
			max-width: 100%;
		}
	}

	&__map {
		&-point {
			position: absolute;
			background-color: #fff;
			z-index: 1;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			opacity: 0.35;
			cursor: pointer;
			transition: 0.4s;

			&.active {
				background-color: $gold;
				opacity: 1;
			}
			&:hover {
				@media screen and (min-width: 1025px) {
					background-color: $gold;
					opacity: 1;
				}
			}

			&:nth-child(2) {
				left: 74.8%;
				top: 62.5%;
			}
			&:nth-child(3) {
				left: 25.3%;
				top: 39.3%;
			}
			&:nth-child(4) {
				left: 59%;
				top: 29%;
			}
			&:nth-child(5) {
				left: 58.7%;
				top: 31.2%;
			}
			&:nth-child(6) {
				left: 57.4%;
				top: 31.4%;
			}
			&:nth-child(7) {
				left: 69.6%;
				top: 28.8%;
			}
			&:nth-child(8) {
				left: 56.8%;
				top: 28%;
			}
			&:nth-child(9) {
				left: 55%;
				top: 24.5%;
			}
			&:nth-child(10) {
				left: 62.1%;
				top: 26%;
			}
			&:nth-child(11) {
				left: 60.7%;
				top: 30%;
			}
			&:nth-child(12) {
				left: 63.3%;
				top: 28%;
			}
			&:nth-child(13) {
				left: 60.3%;
				top: 28%;
			}
		}

		&-wrapper {
			position: absolute;
			top: 126px;
			width: 60.3748vw;
			height: 29.79vw;

			@include media(tablet-993) {
				width: 85vw;
				height: 42vw;
			}

			img {
				position: absolute;
				width: 60.3748vw;
				height: 29.79vw;
				object-fit: cover;
				z-index: 0;

				@include media(tablet-993) {
					width: 100%;
					height: 100%;
				}
			}
		}
	}

	&__list {
		list-style: none;
		max-height: 660px;
		overflow-x: hidden;
		overflow-y: auto;

		&::-webkit-scrollbar {
			width: 3px;
			border-radius: 2px;
		}

		&::-webkit-scrollbar-track {
			-webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
		}

		&::-webkit-scrollbar-thumb {
			background-color: $gold;
			border-radius: 1px;
		}

		&-wrapper {
			position: absolute;
			width: 330px;
			right: -13vw;
			z-index: 5;

			@include media(1430) {
				right: 0;
			}

			@include media(tablet-993) {
				position: static;
				margin-top: 30px;
			}

			@include media(400) {
				width: 270px;
			}
		}

		li {
			cursor: pointer;
			&:not(:first-child) {
				margin-top: 12px;
			}
			a {
				font-family: $secondaryFont;
				font-weight: bold;
				font-size: 18px;
				line-height: 44px;
				color: #ffffff;
				opacity: 0.64;
				white-space: nowrap;

				@include media(400) {
					font-size: 16px;
				}

				&:visited {
					color: inherit;
				}
			}

			&.active {
				a {
					font-size: 27px;
					line-height: 44px;
					color: $gold;
					opacity: 1;

					@include media(400) {
						font-size: 20px;
					}
				}
			}

			&:hover {
				@media screen and (min-width: 1025px) {
					a {
						font-size: 27px;
						line-height: 44px;
						color: $gold;
						opacity: 1;

						@include media(400) {
							font-size: 20px;
						}
					}
				}
			}
		}
	}
}
