.event {
	&-hero {
		position: relative;
		min-height: 601px;

		margin-top: -102px;

		@include media(tablet-993) {
			margin-top: -135px;
			padding-top: 33px;
		}

		&__bg {
			position: absolute;
			z-index: -1;
			height: 100%;

			@include media(tablet-993) {
				margin-top: -33px;
			}
			img {
				object-fit: cover;
				width: 100%;
				height: 601px;

				@include media(tablet-993) {
					min-height: 0;
					height: 100%;
				}
			}
		}

		&__inner {
			padding-top: 105px;

			&-left {
				padding-left: 39px;
				flex-direction: column;
				justify-content: flex-start;

				@include media(mobile) {
					display: none;
				}
			}

			&-right {
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;

				@include media(mobile) {
					flex-basis: 100%;
					padding-left: 39px;
				}

				@include media(550) {
					padding-left: 17px;
				}
			}
		}

		&__title {
			line-height: 44px;
			max-width: 450px;
		}
	}

	&-content {
		color: $textDark;
		padding-bottom: 160px;

		@include media(mobile) {
			padding-bottom: 60px;
		}

		&__inner {
			&-left {
				@include media(mobile) {
					display: none;
				}
			}

			&-right {
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;
				max-width: 727px;

				@include media(mobile) {
					flex-basis: 100%;
					padding-left: 39px;
				}

				@include media(550) {
					padding-left: 17px;
				}
			}
		}

		h1,
		h2,
		h3,
		h4 {
			font-family: $secondaryFont;
			font-weight: bold;

			&:first-child {
				margin-top: 37px;
			}

			&:not(:first-child) {
				margin-top: 64px;

				@include media(mobile) {
					margin-top: 30px;
				}
			}
		}

		h2 {
			font-size: 27px;
			line-height: 43px;

			@include media(mobile) {
				font-size: 24px;
				line-height: 150%;
			}
		}

		h3 {
			font-size: 24px;
			line-height: 40px;

			@include media(mobile) {
				font-size: 21px;
				line-height: 150%;
			}
		}

		h4 {
			font-size: 21px;
			line-height: 37px;

			@include media(mobile) {
				font-size: 18px;
				line-height: 150%;
			}
		}

		p {
			font-weight: normal;
			font-size: 15px;
			line-height: 21px;
			margin-top: 21px;

			@include media(mobile) {
				margin-top: 18px;
			}
		}

		i {
			font-weight: 300;
		}

		u {
			&::after {
				background-color: $dark;
			}
		}

		a {
			&::after {
				transition: 0.3s;
				background-color: #0500eb;
			}

			&:hover {
				&::after {
					width: 0;
				}
			}
		}

		a,
		i,
		b,
		u {
			display: inline-block;
		}

		u,
		a {
			position: relative;
			display: inline-block;
			text-decoration: none !important;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				right: 0;
				bottom: 1px;
				height: 1px;
				width: 100%;
			}
		}

		a {
			font-style: italic;
			font-weight: 500;
			font-size: 15px;
			line-height: 21px;
			text-decoration-line: underline;
			color: #0500eb;
		}

		p {
			a,
			b,
			i,
			u {
				margin-top: 33px;
			}
		}

		a,
		b,
		i,
		u {
			margin-top: 54px;
		}

		img {
			margin-top: 97px;

			@include media(mobile) {
				margin-top: 60px;
			}
		}

		ul,
		ol {
			font-family: $secondaryFont;
			font-weight: bold;
			font-size: 27px;
			line-height: 43px;
			margin-top: 64px;
			list-style: inside;

			@include media(mobile) {
				font-size: 24px;
				line-height: 150%;
				margin-top: 40px;
			}

			li {
				font-family: $mainFont;
				font-weight: normal;
				font-size: 15px;
				line-height: 32px;

				&:first-child {
					margin-top: 22px;
				}
			}
		}

		ol {
			list-style: inside;
			list-style-type: decimal;
		}

		table {
			width: 100%;
			border-spacing: 6px;
			text-align: left;
			font-family: $secondaryFont;
			margin-left: -6px;
			margin-top: 47px;

			thead {
				th {
					padding: 9px 10px 9px 16px;
					font-weight: bold;
					font-size: 18px;
					line-height: 43px;

					background: #f0f0f0;

					&:first-child {
						width: 74.54%;
						max-width: 650px;
					}

					&:last-child {
						width: 25.46%;
						max-width: 216px;
					}
				}
			}

			tbody {
				font-size: 18px;
				line-height: 43px;
				tr {
					&:first-child {
						margin-top: -6px;
					}
				}
				td {
					padding: 9px 10px 9px 16px;
					background-color: #fff;
				}
			}
		}
	}
}
